.main {
  position: relative;
  background-image: url(../img/main-img.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 100px 0 50px;
  color: #fff;
  &__text {
    max-width: 360px;
    font-size: 16px;
    line-height: 125%;
    letter-spacing: -0.32px;
    color: #BA9166;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 20px;
    margin-top: 245px;
  }
  &__title-wrap {
    position: relative;
  }
  &__title {
    font-size: 96px;
    line-height: 88%;
    font-weight: 400;
    letter-spacing: -6.72px;
    span {
      color: #BA9166;
    }
  }
  &__title-img {
    position: absolute;
    top: 95px;
    left: 342px;
    width: 130px;
    img {
      width: 100%;
    }
  }
  &__group {
    flex-shrink: 0;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 4px / 2));
    gap: 4px;
    width: 422px;
  }
  .select {
    &-show {
      min-height: 56px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
      background: rgba(0, 0, 0, 0.4);
      backdrop-filter: blur(40px);
      padding: 12px 14px;
    }
    &-title {
      font-size: 10px;
      line-height: 100%;
      font-weight: 500;
      letter-spacing: -0.2px;
      color: #B5B5B5;
      margin-bottom: 5px;
    }
    &-current {
      font-size: 12px;
      font-weight: 500;
      letter-spacing: -0.48px;
    }
    &-arrow {
      position: relative;
      right: unset;
      bottom: unset;
      flex-shrink: 0;
    }
    &-hidden {
      background: rgba(0, 0, 0, 0.4);
      backdrop-filter: blur(40px);
      padding-top: 20px;
    }
    &-item {
      font-size: 12px;
      letter-spacing: -0.24px;
    }
  }
  .select:nth-child(1) {
    grid-column: 1/3;
  }
  &__btn {
    min-height: 56px;
    flex-direction: column;
    align-items: start;
    border-radius: 8px;
    padding: 5px 10px;
    &:hover {
      background: rgba(22, 70, 126, 0.8);
      color: #fff;
    }
    .btn-title {
      font-size: 14px;
      font-weight: 600;
      letter-spacing: -0.28px;
    }
    .btn-text {
      font-size: 10px;
      font-weight: 500;
      letter-spacing: -0.2px;
      color: #BA9166;
    }
  }
}

@media screen and (max-width: 1200px) {
  .main {
    &__wrap {
      margin-top: 200px;
    }
    &__title {
      font-size: 64px;
      letter-spacing: -3.84px;
    }
    &__title-img {
      top: 66px;
      left: 230px;
      width: 90px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding: 325px 0 25px;
    &__text {
      display: none;
    }
    &__wrap {
      margin-top: 0;
    }
    &__title {
      font-size: 44px;
      letter-spacing: -2.64px;
    }
    &__title-img {
      display: none;
    }
    &__group {
      width: 380px;
    }
    .select {
      &-show {
        min-height: 48px;
        padding: 5px 10px;
      }
    }
    &__btn {
      min-height: 48px;
      .btn-title {
        font-size: 10px;
        letter-spacing: -0.2px;
      }
      .btn-text {
        font-size: 8px;
        letter-spacing: -0.16px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    &__wrap {
      flex-direction: column;
      align-items: start;
      gap: 70px;
    }
    &__group {
      width: 100%;
    }
  }
}

@media screen and (max-width: 450px) {
  .main {
    background-image: url(../img/main-img--mob.webp);
  }
}