.wrapper {
  overflow: hidden;
}
.select {
  position: relative;
  width: 100%;
  &--active {
    z-index: 3;
    .select-current {
      color: rgba(255, 255, 255, 0.4);
    }
    .select-arrow {
      &::before {
        opacity: 1;
      }
      img {
        transform: rotate(-180deg);
      }
    }
    .select-hidden {
      transform: translateY(0);
      opacity: 1;
      pointer-events: auto;
    }
  }
  &-show {
    position: relative;
    z-index: 2;
    min-height: 48px;
    background: #16467E;
    border-radius: 10px;
    padding: 10px 30px 10px 10px;
    cursor: pointer;
  }
  &-current {
    font-size: 14px;
    line-height: 100%;
    font-weight: 600;
    letter-spacing: -0.28px;
  }
  &-arrow {
    position: absolute;
    right: 10px;
    bottom: 10px;
    width: 10px;
    display: flex;
    align-items: center;
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 20px;
      height: 20px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 4px;
      opacity: 0;
      transition: all 0.125s;
    }
    img {
      position: relative;
      z-index: 2;
      width: 100%;
      transition: all 0.3s;
    }
  }
  &-hidden {
    position: absolute;
    top: calc(100% - 15px);
    left: 0;
    transform: translateY(-8px);
    width: 100%;
    background: #16467E;
    border-radius: 0 0 10px 10px;
    padding: 30px 10px 10px 10px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.125s;
  }
  &-items {
    display: grid;
    grid-template-columns: 100%;
  }
  &-item {
    font-size: 14px;
    line-height: 100%;
    font-weight: 600;
    letter-spacing: -0.28px;
    padding: 10px 0;
    cursor: pointer;
    &:not(:last-child) {
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    }
  }
  &-item__btn {
    text-align: start;
    background: transparent;
    padding: 0;
    color: #fff;
  }
  .btn-title {
    display: block;
    font-size: 14px;
    line-height: 100%;
    font-weight: 600;
    letter-spacing: -0.28px;
  }
  .btn-text {
    font-size: 10px;
    line-height: 100%;
    font-weight: 500;
    letter-spacing: -0.2px;
    color: #BA9166;
  }
}
.btn-blue {
  background: #16467E;
  border: 1px solid #16467E;
  color: #fff;
  &:hover {
    background: transparent;
    color: #16467E;
  }
  span {
    line-height: 120%;
    font-weight: 500;
  }
}
.btn-white {
  background: #fff;
  color: #16467E;
  &:hover {
    background: #16467E;
    color: #fff;
  }
  span {
    line-height: 120%;
  }
}

@media screen and (max-width: 1100px) {
  .select {
    &-current {
      font-size: 13px;
    }
    &-item {
      font-size: 13px;
    }
    .btn-title {
      font-size: 13px;
    }
  }
}

@media screen and (max-width: 576px) {
  .select {
    &-show {
      min-height: 40px;
      padding: 8px 26px 8px 8px;
    }
    &-current {
      font-size: 10px;
      letter-spacing: -0.2px;
    }
    &-item {
      font-size: 10px;
      letter-spacing: -0.2px;
    }
    .btn-title {
      font-size: 10px;
      letter-spacing: -0.2px;
    }
    .btn-text {
      font-size: 8px;
      letter-spacing: -0.16px;
    }
    &-hidden {
      padding-top: 20px;
    }
  }
}